.claims-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

h1 {
  font-size: 2.5em;
  margin-bottom: 20px;
  color: #333;
  text-align: center;
}

.controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.controls input {
  width: 60%;
  padding: 10px;
  font-size: 1em;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.controls .create-button {
  padding: 10px 20px;
  font-size: 1em;
  color: white;
  background-color: #28a745;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.controls .create-button:hover {
  background-color: #218838;
}

.claims-table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 20px;
}

.claims-table th,
.claims-table td {
  padding: 10px;
  border: 1px solid #ddd;
  text-align: left;
}

.claims-table th {
  background-color: #4CAF50;
  color: white;
}

.claims-table tr:nth-child(even) {
  background-color: #f2f2f2;
}

.claims-table .edit-button,
.claims-table .delete-button {
  padding: 5px 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 0.9em;
}

.claims-table .edit-button {
  background-color: #007bff;
  color: white;
  margin-right: 5px;
}

.claims-table .edit-button:hover {
  background-color: #0056b3;
}

.claims-table .delete-button {
  background-color: #dc3545;
  color: white;
}

.claims-table .delete-button:hover {
  background-color: #c82333;
}

.panel {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.panel-content {
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  width: 400px;
  max-width: 100%;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  position: relative;
}

.panel-content .close-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 1.5em;
  cursor: pointer;
}

.panel-content h2 {
  margin-top: 0;
}

.panel-content form {
  display: flex;
  flex-direction: column;
}

.panel-content form input {
  margin-bottom: 10px;
  padding: 10px;
  font-size: 1em;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.panel-content .save-button {
  padding: 10px;
  font-size: 1em;
  color: white;
  background-color: #28a745;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.panel-content .save-button:hover {
  background-color: #218838;
}

.error-message {
  color: red;
  margin-bottom: 10px;
}

.delete-confirm {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.delete-confirm-content {
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  width: 300px;
  max-width: 100%;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.delete-confirm-content p {
  margin-bottom: 20px;
}

.confirm-button,
.cancel-button {
  padding: 10px 20px;
  font-size: 1em;
  margin: 0 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.confirm-button {
  background-color: #dc3545;
  color: white;
}

.confirm-button:hover {
  background-color: #c82333;
}

.cancel-button {
  background-color: #6c757d;
  color: white;
}

.cancel-button:hover {
  background-color: #5a6268;
}

/* Responsive design */
@media (max-width: 768px) {
  .controls {
    flex-direction: column;
    align-items: stretch;
  }

  .controls input {
    width: 100%;
    margin-bottom: 10px;
  }

  .controls .create-button {
    width: 100%;
  }

  .claims-table th,
  .claims-table td {
    display: block;
    width: 100%;
  }

  .claims-table th {
    text-align: center;
  }

  .claims-table td {
    text-align: center;
  }

  .claims-table {
    display: block;
    overflow-x: auto;
  }
}
